import {reactive,toRefs,onMounted,defineComponent} from 'vue';
import {useRouter} from 'vue-router';
import {getRequest,postRequest} from '../../request/api';
import url from '../../request/url';
import {Toast} from 'vant';
import BgBox from '../../components/BgBox';
import ProductItem from '../../components/ProductItem';
export default defineComponent({
    name:'like',
    components:{
        BgBox,
        ProductItem
    },
    setup () {
        let state = reactive({
            likeProduct:[]
        })

        let {back,getLikeProduct,goDetail,removeLikeProduct} = methodes(state);

        onMounted(() => {
            getLikeProduct();
        })

        return {
            ...toRefs(state),
            back,
            getLikeProduct,
            goDetail,
            removeLikeProduct
        }
    }
})

function methodes (state) {
    let router = useRouter();
    let back = () => {
        router.back();
    }

    //获取收藏商品
    let getLikeProduct = () => {
        getRequest(url.findAllLike).then(({code,result=[]}) => {
            if(code == 2000) {
                Reflect.set(state,'likeProduct',result)
            }else{

            }
            
        }).catch((err) => {
            console.log("err==>",err);
        });
    }

    let goDetail = (pid) => {
        router.push({name: 'detail', params: {pid}});
    }

    let removeLikeProduct = (pid,index) => {
        postRequest(url.notlike,{pid}).then(({code}) => {
            if(code == 900){
                Toast('删除成功');
                Reflect.apply(state.likeProduct.splice,state.likeProduct,[index,1]);
            }else{
                Toast('删除失败');
            }
        }).catch((err) => {
            console.log("err==>",err);
        });
    }

    return{
        back,
        getLikeProduct,
        goDetail,
        removeLikeProduct
    }
}